@import url('https://fonts.googleapis.com/css2?family=Open+Sans:wght@400;600;700&display=swap');

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

html {   
  height: 100%;
  scroll-behavior: smooth;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

body {
  height: 100%;
  font-size: 16px;
  font-family: 'Open Sans', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: #000;
  color: #909296;
  font-weight: 600;
}

#root {
  display: flex;
  flex-direction: column;
  min-height: 100%;
}

.main {
  display: flex;
  flex: 1 1 auto;
  flex-direction: column;
}

h1, h2, h3, h4, h5, h6 {
  color: #fff;
}

a {
  font-size: 16px;
  font-weight: 600;
}

button {
  cursor: pointer;
}

.button--outline {
  border-color: #fff;
  color: #fff;
}

.button--subtle {
  color: #fff;
}

.button--subtle:hover {
  color: #000;
}

.container {
  max-width: 1200px;
  width: 100%;
  padding: 0 15px;
  margin: 0 auto;
  overflow: hidden;
}

.page-wrapper {
  position: relative;
  overflow: hidden;
  z-index: 10;
}

.page-login, .page-signup {
  justify-content: center;
}

