@keyframes rotating {
  from{
      transform: rotate(0deg);
  }
  to{
      transform: rotate(360deg);
  }
}

.animated-background {
  width: 100%;
  height: 100%;
  position: relative;
  overflow: hidden;
}

.animated-background::after {
  content: '';
  width: 105%;
  height: 100%;
  position: absolute;
  z-index: 1;
  right: 0;
  top: 0px;
  background: url('./assets/noise.svg');
  background-size: 10%;
}

.animated-background::before {
  content: '';
  position: absolute;
  bottom: -160%;
  right: -30%;
  width: 1000px;
  height: 1000px;
  animation: rotating 23s infinite ease-in-out;
  background: linear-gradient(180deg, #FF6161 0%, #43FFDD 35.88%, #45BCFF 67.65%, #D795FF 100%);
  mix-blend-mode: normal;
  filter: blur(132.344px);
  border-radius: 20%;
  transform: matrix(0, 1, 1, 0, 0, 0);
}

.animated-background--topLeft:before {
  top: -600px;
  left: -40%;
}

.animated-background--bottomRight:before {
  bottom: -600px;
  right: -40%;
}